@import url('https://fonts.googleapis.com/css?family=Fira+Mono');
@import url('https://fonts.googleapis.com/css?family=Alfa+Slab+One');

body {
  font-family: 'Fira Mono', monospace;
}

.App {
  text-align: center;
}


.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }

}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }

}

.display-panel-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
}

.display-panel {
  background-color: brown;
  border: 3px solid brown;
  border-radius: 5px;
  font-size: 20px;
  margin-bottom: 4px;
  font-weight: 700;
  width: 40px;
  height: 38px;
  text-align: center;
  padding: 4px;
  color: white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.control-panel {
  padding: 20px;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 14px solid gray;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.brand {
  font-size: 18px;
  font-weight: 200;
  margin-top: 10px;
  margin-bottom: 5px;
  font-family: "Alfa Slab One", cursive;
  font-size: 3.1rem;
}

.control-panel .row {
  display: -ms-flexbox;
  display: flex;
}
.control-panel .row.btns-row {
  -ms-flex-align: center;
      align-items: center;
}

.control-btn button {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 4px solid black;
  background: yellow;
  margin: 4px;
  margin-bottom: 12px;
  cursor: pointer;
}

.switch-btn-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 10px;
}

.switch-btn {
  width: 40px;
  height: 16px;
  border: 2px solid gray;
  background: gray;
  border-radius: 2px;
  display: inline-block;
  margin: 0 4px;
}

.control-btn{
  font-size: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin: 8px;
  
}


.switch-btn-container .knob {
  width: 20px;
  height: 100%;
  background-color: #3193DE;
  border-radius: 4px;
}

.switch-btn-container .knob.on {
  margin-left: auto;
}

.buttons-container {
  width: 500px;
  height: 500px;
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  overflow: hidden;
  border: 20px solid gray;
}

.color-btn {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  min-width: 250px;
  min-height: 250px;
  background-color: red;
  cursor: pointer;
}

.color-btn.btn-1 {
  background-color: #025302;
  border-right: 10px solid gray;
  border-bottom: 10px solid gray;
}

.color-btn.btn-1.active {
  background-color: #1bd24d;
}

.color-btn.btn-2 {
  background-color: #6d0000;
  border-left: 10px solid gray;
  border-bottom: 10px solid gray;
}

.color-btn.btn-2.active {
  background-color: red;
}

.color-btn.btn-3 {
  background-color: #5f5f01;
  border-right: 10px solid gray;
  border-top: 10px solid gray;
}

.color-btn.btn-3.active {
  background-color: yellow;
}

.color-btn.btn-4 {
  background-color: #000053;
  border-left: 10px solid gray;
  border-top: 10px solid gray;
}

.color-btn.btn-4.active {
  background-color: blue;
}

.control-panel {
  background: white;
  position: absolute;
  left: calc(50% - 125px);
  top: calc(50% - 125px);
}

.game-container {
  width: 524px;
  position: relative;
  margin: 50px auto;

}

.display-panel__label {
  text-align: center;
  font-size: 12px;  
}


